import axios from "@/aixos"
import { getToken, setToken, removeToken } from '@/utils/auth'
const state = {
    token:getToken(),
    roles:[]
}
const mutations = {
  SET_TOKEN: (state, token) => {
    state.token = token
  },
  SET_ROLES: (state, roles) => {
    state.roles = roles
  }
}
const actions = {
  login({ commit }, userInfo) {
    return new Promise((resolve, reject) => {
        axios.post('/owner/sign',userInfo).then(res=>{
          const { data } = res
          data.roles='admin'
          commit('SET_TOKEN', data.token)
          commit('SET_ROLES', ['admin'])
          setToken(data.token)
          resolve(data)
        }).catch(error=>{
          reject(error)
        })
    })
  },
  // get user info
  getInfo({commit}) {
    return new Promise((resolve, reject) => {
      axios.post('/owner').then(res=>{
        const { data } = res
        data["roles"]='admin'
        commit('SET_ROLES', ['admin'])
        resolve(data)
      }).catch(error=>{
        reject(error)
      })
    })
  },
  // user logout
 

  // remove token
  resetToken({ commit }) {
    return new Promise(resolve => {
      commit('SET_TOKEN', '')
      commit('SET_ROLES', [])
      removeToken()
      resolve()
    })
  }

}

export default {
  namespaced: true,
  state,
  actions,
  mutations,
}