<template>
  <div>
    <div id="main" ref="main"></div>
  </div>
</template>

<script>
import resize from 'element-resize-detector'
var b = resize()
export default {
   data(){
        return {
           option: {
             
                title: {
                    text: '年销量百分比',
                  
                    left: 'center'
                },
                tooltip: {
                    trigger: 'item',
                    formatter: '{a} <br/>{b} : {c} ({d}%)'
                },
                legend: {
                      top: 'bottom',
                      
                  },
               
                series: [
                    {
                    name: '总销量百分比',
                    type: 'pie',
                    radius: [20, 140],
                    center: ['50%', '50%'],
                    roseType: 'radius',
                    
                    
                    emphasis: {
                        label: {
                        show: true
                        }
                    },
                    data: [
                        { value: 32680, name: '奶茶' },
                        { value: 29550, name: '拿铁' },
                        { value: 23190, name: '芝芝可可' },
                        { value: 14580, name: '圣代' },
                    
                    ]
                    }
                ]
           } 

        }
    },
    mounted() {
     this.add()
    },
    methods:{
        add(){
             let a =this.$refs.main
            const myChart = this.$echarts.init(a)
            b.listenTo(a,function(){
                myChart.resize()
            })
            myChart.setOption(this.option);
        }
    }
}
</script>

<style scoped>
#main{
    width: 100%;
    height:500px;
    margin-top: 40px;
}
</style>