<template>
 <div id="hig">
  <div class="demo-input-size">
    <el-input style="width:25%;"
      filterable
      placeholder="输入用户id查询"
      prefix-icon="el-icon-search"
      v-model="value1">
    </el-input>
    <el-input style="width:25%; margin-left:20px"
      placeholder="输入用户名查询"
      prefix-icon="el-icon-search"
      v-model="value2">
    </el-input>
    <el-input style="width:25%; margin:20px"
      placeholder="输入手机号查询"
      prefix-icon="el-icon-search"
      v-model="value3">
    </el-input>
    <el-button @click="filterMethod">查询</el-button>
    <el-button @click="btncz">重置</el-button>
  </div>
  <el-table
    :data="obj"
    height="581"
    border
    style="width: 100%">
    <el-table-column
      prop="_id"
      label="id编号"
      width="85">
    </el-table-column>
    <el-table-column
      prop="username"
      label="姓名"
      width="120">
    </el-table-column>
    <el-table-column
      prop="phone_number"
      label="手机号"
      width="180">
    </el-table-column>
    <el-table-column
      prop="wallet"
      label="钱包"
      width="120">
    </el-table-column>
    <el-table-column
      prop="creat_time"
      sortable
      label="注册时间">
    </el-table-column>
    <el-table-column
      sortable
      prop="update_time"
      label="更改信息时间">
    </el-table-column>

    <el-table-column
      label="更多操作">
      <template slot-scope="scope">

        <el-button type="text" @click="getUser(scope.row)" style="margin-right:10px">查看</el-button>
        
        <el-button type="text" style="margin-right:10px" @click='modification(scope.row)'>编辑</el-button>

        <el-button type="text" style="margin-right:10px" @click="dialogVisibleth(scope.row)">删除</el-button>

        <el-button type="text" style="margin-right:10px" @click="dialogVisiblesi(scope.row)">修改密码</el-button>
        
      </template>
      
    </el-table-column>
  </el-table>


  <el-dialog style="margin-top: -12.5vh; background-color:transparent;"
          title="个人信息"
          :visible.sync="dialogVisible1"
          width="45%">
          <span>
          <el-card class="box-card" style="padding:20px" >
                <el-button type="text" class="el-btn">
                  <span>
                    <img class="head-img" :src=user.avarat>
                  </span>
                </el-button>
                <div class="grxx">
                    <div class="el-icon-s-custom">登录账号</div>
                    <div>{{user.username}}</div>
                </div>
                <el-divider class="br"></el-divider>
                <div class="grxx1">
                    <div class="el-icon-unlock">用户权限</div>
                    <div>{{user.age}}</div>
                </div>
                <el-divider class="br"></el-divider>
                <div class="grxx1">
                    <div class="el-icon-phone ">电话号码</div>
                    <div>{{user.phone_number}}</div>
                </div>
                <el-divider class="br"></el-divider>
                <div class="grxx1">
                    <div class="el-icon-s-cooperation ">账户余额</div>
                    <div>{{user.wallet}}</div>
                </div>
                <el-divider class="br"></el-divider>
              
                <div class="grxx1">
                    <div class="el-icon-user-solid ">性别</div>
                    <div>{{user.gender=='1' ? '男':"女"}}</div>
                </div>
                <el-divider class="br"></el-divider>
                <div class="grxx1">
                    <div class="el-icon-date">注册时间</div>
                    <div>{{user.creat_time}}</div>
                </div>
                <el-divider class="br"></el-divider>
          </el-card>
          </span>
          <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible1 = false">取 消</el-button>
            <el-button type="primary" @click="dialogVisible1 = false">确 定</el-button>
          </span>
        </el-dialog>



        <el-dialog
          title="个人信息修改"
          :visible.sync="dialogVisible2"
          width="30%">
          <span>
            <el-form label-width="80px">
            <el-form-item label="用户名">
              <el-input v-model="userobj.username"></el-input>
            </el-form-item>
            <el-form-item label="电话号码">
              <el-input v-model='userobj.phone_number'></el-input>
            </el-form-item>
             <div class="demo-input-suffix">
            性别 :
              <el-radio v-model="userobj.gender" label="男">男</el-radio>
              <el-radio v-model="userobj.gender" label="女">女</el-radio>
            </div>
          </el-form>
          
          </span>
          <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible2 = false">取消修改</el-button>
            <el-button type="primary" @click="updateuser">确定修改</el-button>
          </span>
        </el-dialog>



        <el-dialog
          title="删除用户"
          :visible.sync="dialogVisible3"
          width="30%">
          <span>
          
          <el-form label-width="80px">
            <el-form-item label="用户名:">
              <el-input v-model="remove1.username" disabled></el-input>
            </el-form-item>
            <el-form-item label="用户密码:">
              <el-input v-model='password' type="password"></el-input>
            </el-form-item>
             <div class="demo-input-suffix" style="margin-left:15px">
              <span style="color:red">*</span>请确认：
              <el-radio-group v-model="radio1">
                <el-radio-button label="确认删除"></el-radio-button>
                <el-radio-button label="取消删除"></el-radio-button>
              </el-radio-group>
            </div>
          </el-form>
          </span>
          <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible3 = false">取 消</el-button>
            <el-button type="primary" @click="removeuser">确 定</el-button>
          </span>
        </el-dialog>


        <el-dialog
          title="提示"
          :visible.sync="dialogVisible4"
          width="30%">
          <span>
          
          
          <el-form label-width="80px">
            <el-form-item label="用户名:">
              <el-input v-model="gpassword.username" disabled></el-input>
            </el-form-item>
            <el-form-item label="旧密码:">
              <el-input v-model='oldpassword' type="password"></el-input>
            </el-form-item>
            <el-form-item label="新密码:">
              <el-input v-model='newpassword' type="password"></el-input>
            </el-form-item>
          </el-form>
          
          
          </span>
          <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible4 = false">取 消</el-button>
            <el-button type="primary" @click="modificationpass">确 定</el-button>
          </span>
        </el-dialog>
 
 </div>
</template>


<script>
import md5 from 'md5'
  export default {
    created(){
        this.$http.get('/api/test').then(res=>{
          res.data.forEach(item=>{
            item.creat_time=this.newtime(item.creat_time)
            item.update_time=this.newtime( item.update_time)
          })
          this.obj=res.data
          this.arr=res.data
        })

    },
    inject:['reload','newtime'],
    data(){ 
        return {
          
          radio1:'取消删除',
          value1:'',
          value2:'',
          value3:'',
          password:'',
          oldpassword:'',
          newpassword:'',
          obj:[],
          user:{},
          arr:[],
          userobj:{},
          remove1:{},
          gpassword:{},
          dialogVisible1: false,
          dialogVisible2: false,
          dialogVisible3: false,
          dialogVisible4: false,
          
        }
    },
    methods:{
        dialogVisibleth(item){
          item.id=item._id
          this.password=''
          this.remove1=item
          this.dialogVisible3=true
          this.radio1='取消删除'
        },
        getUser(item){
          this.dialogVisible1 = true
          let id=item._id
          this.$http.post('/owner',{id}).then(res=>{
            console.log(res);
            res.data.update_time=this.newtime(res.data.update_time)
            res.data.creat_time=this.newtime(res.data.creat_time)
            this.user={...res.data}
          })
        },
        modification(item){
          this.dialogVisible2 = true
          item.id=item._id
          this.userobj={...item}
        },
        dialogVisiblesi(item){
          this.dialogVisible4=true
          this.oldpassword=''
          this.newpassword=''
          this.gpassword=item
        },
        modificationpass(){
          if(this.oldpassword==''){
            this.$message({
                  message: '旧密码不能为空',
                  type: 'error'
                  });
                return
          } 
          if(this.newpassword==''){
            this.$message({
                  message: '新密码不能为空',
                  type: 'error'
                  });
                return
          }
          let data={}
          data.username=this.gpassword.username
          data.password=md5(this.oldpassword)
          data.newpassword=md5(this.newpassword)
          this.$http.put('/owner/paword',data).then(res=>{
            if (res.data.code==200){
                  this.$message({
                              message: '密码修改成功',
                              type: 'success'
                              });
                  this.reload()
              }else{
                  this.$message.error(`${res.data.msg}`);
                  return
              }
            this.dialogVisible4=false
          })
        },
        updateuser(){
          let data=this.userobj
          this.$http.put('/owner/modify',data).then(res=>{
            if (res.data.code==200){
                  this.$message({
                              message: '信息修改成功',
                              type: 'success'
                              });
                  this.reload()
              }else{
                  this.dialogVisible2 = true
                  this.$message.error(`${res.data.msg}`);
                  return
              }
          })
        },
        removeuser(){
          
          if(this.radio1=='确认删除'){
            let data=this.remove1
            data.password=this.password
           
            data.password=md5(data.password)
            this.$http.delete('owner/delete',{data}).then(res=>{
              if (res.data.code==200){
                  this.$message({
                              message: '用户删除成功',
                              type: 'success'
                              });
                  this.reload()
              }else if(res.data.code==-2){
                  console.log(res.data.msg);
                  return
              }else{
                this.$message.error(`${res.data.msg}`);
                return
              }
            })
          }else{
            this.$message.error('请确认是否删除');
            return
          }
        },
        filterMethod(){
            let data = [...this.arr]
            
            let a= data.findIndex((value,index)=>{
              return value._id==this.value1
            })
            let b= data.findIndex((value,index)=>{
              return value.username==this.value2
            })
            let c= data.findIndex((value,index)=>{
              return value.phone_number==this.value3
            })
            let sech=[a,b,c]
            let index=sech.find((n)=>n>-1)

            if(index!=undefined){
               console.log(index);
              this.obj=[data[index]]
              this.value1=''
              this.value2=''
              this.value3=''
            }else{
              console.log(index);
              this.obj=[]
              this.value1=''
              this.value2=''
              this.value3=''

            }

        },
        btncz(){
            this.obj=this.arr
            this.value1=''
            this.value2=''
            this.value3=''
        },
        

      },
      
    
  }
   
    
</script>






<style scoped>
.demo-input-suffix{
    margin: 30px;
}
  .my-label {

    background: #E1F3D8;
  }
#hig{
  width: 97%;
  height: 583px ;
  margin-left: 20px;
 
}

  .my-content {
    background: #FDE2E2;
  }
  .grxx1{
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 13px;
}
.grxx{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 30px;
  font-size: 13px;
}
.el-btn{
  width: 30%;
  margin-left: 35% !important;
}
  .el-row {
    margin-bottom: 20px;
 
  }
  
  .el-col {
    border-radius: 4px;
    margin-left: 20px;
    margin-top: 20px;
  }
  .bg-purple-dark {
    background: #99a9bf;
  }

  .bg-purple-light {
    background: #e5e9f2;
  }

  .row-bg {
    padding: 10px 0;
    background-color: #f9fafc;
  }
  .head-img{
    width: 50%;
    height: 50%;
    border-radius: 50%;
  }

.br{
  margin: 13px 0 !important;
}
 .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 100px;
    margin-top: 40%;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>
