<template>
<div id="hig">
<el-table
    :data="tableData.slice((currentPage-1)*pagesize,currentPage*pagesize)"
    style="width: 100%">
    <el-table-column
      label="id编号"
      width="100"
      prop="_id">
    </el-table-column>
    <el-table-column
      label="商品名称"
      width="220"
      prop="name">
    </el-table-column>
    <el-table-column
      label="单价"
      width="100"
      prop="unit_price">
    </el-table-column>
    <el-table-column
      label="商品上传时间"
      prop="creat_time">
    </el-table-column>
    <el-table-column
      label="商品信息更新时间"
      prop="update_time">
    </el-table-column>
    <el-table-column
      align="right">
      <template slot="header" slot-scope="scope">
        <el-input id="einp"  style="width:200px"
          v-model="search"
          size="mini"
          placeholder="输入关键字搜索"/>
           
      </template>
      <template slot-scope="scope">
        <el-button
          size="mini"
          type="info"
          @click="handleEdit(scope.row)">查看</el-button>
        <el-button
          size="mini"
          @click="modification(scope.row)">修改</el-button>
        <el-button
        size="mini"
        type="danger"
        @click="dialogVisibleth(scope.row)">删除</el-button>
      </template>
    </el-table-column>
  </el-table>
  <el-dialog style="margin-top: 0; background-color:transparent;"
          title="商品详情"
          :visible.sync="dialogVisible1"
          width="45%">
          <span>
          <el-row>
            <el-col :span="22">
              <el-card :body-style="{ padding: '0px' }">
                <div style="display:flex;">
                    <div style="width:200px;height:230px">
                      <img style="border-radius:5px;" src="https://gimg2.baidu.com/image_search/src=http%3A%2F%2Fimgpp.ztupic.com%2Fbup%2Fso%2F20210622%2Fa64c8edc85bb765346c93d7b6f657cde-1.jpg%3Fx-oss-process%3Dimage%2Fcrop%2Cw_1024%2Ch_1542%2Fresize%2Ch_812%2Fquality%2Cq_85%2Fsharpen%2C100&refer=http%3A%2F%2Fimgpp.ztupic.com&app=2002&size=f9999,10000&q=a80&n=0&g=0n&fmt=auto?sec=1661131958&t=ab3ff8be44b6d9f440e562aa5bc24f5a" class="image">
                    </div>
                    <div style="padding: 15px;margin-left:20px">
                      <span style="font-weight: 600;">商品介绍</span>
                      <div class="bottom clearfix">
                        <p><span>商品名称：</span><span>{{commodity.name}}</span></p>
                        <p><span>商品编号：</span><span>{{commodity.id}}</span></p>
                        <p><span>商品价格：</span><span>{{commodity.unit_price}}</span></p>
                        <p><span>商品产地：</span><span>中国大陆</span></p>
                        <p><span>商品上架时间：</span><span>{{commodity.creat_time}}</span></p>
                        <p><span>商品更新时间：</span><span>{{commodity.update_time}}</span></p>
                      </div>
                    </div>
                </div>
                
              </el-card>
            </el-col>
          </el-row>
          </span>
          <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible1 = false">取 消</el-button>
            <el-button type="primary" @click="dialogVisible1 = false">确 定</el-button>
          </span>
        </el-dialog>

        <el-dialog
          title="商品信息修改"
          :visible.sync="dialogVisible2"
          width="30%">
          <span>
            <el-form label-width="80px">
                <el-form-item label="商品编号:">
                  <el-input v-model='commodityobj.id' disabled></el-input>
                </el-form-item>
                <el-form-item label="商品名称:">
                  <el-input v-model="commodityobj.name"></el-input>
                </el-form-item>
                <el-form-item label="商品单价:">
                  <el-input v-model="commodityobj.unit_price"></el-input>
                </el-form-item>
            </el-form>
          
          </span>
          <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible2 = false">取消修改</el-button>
            <el-button type="primary" @click="updateuser">确定修改</el-button>
          </span>
        </el-dialog>



        <el-dialog
          title="下架商品"
          :visible.sync="dialogVisible3"
          width="30%">
          <span>
          <el-form label-width="80px">
                <el-form-item label="商品编号:">
                  <el-input v-model='remove1.id' disabled></el-input>
                </el-form-item>
                <el-form-item label="商品名称:">
                  <el-input v-model="remove1.name" disabled></el-input>
                </el-form-item>
                <el-form-item label="商品单价:">
                  <el-input v-model="remove1.unit_price" disabled></el-input>
                </el-form-item>
                <div class="demo-input-suffix" style="margin-left:15px">
              <span style="color:red">*</span>请确认：
              <el-radio-group v-model="radio1">
                <el-radio-button label="确认删除"></el-radio-button>
                <el-radio-button label="取消删除"></el-radio-button>
              </el-radio-group>
            </div>
          </el-form>
          </span>
          <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible3 = false">取 消</el-button>
            <el-button type="primary" @click="removecomdity">确 定</el-button>
          </span>
        </el-dialog>

        <el-dialog
          title="新增商品"
          :visible.sync="dialogVisible4"
          width="30%">
          <span>
            <el-form label-width="80px">
                <el-form-item label="商品名称:">
                  <el-input v-model="addcommdity1.name"></el-input>
                </el-form-item>
                <el-form-item label="商品单价:">
                  <el-input v-model="addcommdity1.unit_price"></el-input>
                </el-form-item>
                 <el-form-item label="商品产地:">
                  <el-input v-model='addcommdity1.position'></el-input>
                </el-form-item>
            </el-form>
          
          </span>
          <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible4 = false">取消添加</el-button>
            <el-button type="primary" @click="addcommdity">确定添加</el-button>
          </span>
        </el-dialog>
        
  <div style="margin-top:.8rem;margin-left: .2rem; position:relative;">
   <el-pagination 
     @size-change="handleSizeChange"
     @current-change="handleCurrentChange"
     :current-page="currentPage"
     :page-sizes="[5,8,10]"
     :page-size="pagesize"
     layout="total, sizes, prev, pager, next, jumper"
     :total="tableData.length">
   </el-pagination>
   <el-button type="primary" size="mini" style="position:absolute;top:0;right: 9px;" @click="dialogVisible4 = true">新增商品</el-button>
 </div>
</div>
  
  
</template>


<script>
  export default {
    inject:['reload','newtime'],
    created(){
      this.$http.get('/commodity').then(res=>{
        res.data.forEach(item=>{
          item.creat_time=this.newtime(item.creat_time)
          item.update_time=this.newtime(item.update_time)
          item.unit_price=item.unit_price
        })
        this.tableData=res.data
        this.arr=res.data
      })
    },
    data() {
      return {
        arr:[],
        tableData: [],
        currentPage: 1,
        currentIndex: '',
        pagesize: 8,
        search: '',
        commodity:{},
        commodityobj:{},
        remove1:{},
        addcommdity1:{},
        radio1:'取消删除',
        dialogVisible1: false,
        dialogVisible2: false,
        dialogVisible3: false,
        dialogVisible4: false,
      }
    },
    methods: {
      handleEdit(item) {
          this.dialogVisible1 = true
          item.id=item._id
          this.commodity=item

      },
      dialogVisibleth(item){
          item.id=item._id
          this.remove1=item
          this.dialogVisible3=true
          this.radio1='取消删除'
      },
      removecomdity(){
          if(this.radio1=='确认删除'){
            let data=this.remove1
            this.$http.delete('/commodity',{data}).then(res=>{
              if (res.data.code==200){
                  this.$message({
                              message: '删除成功',
                              type: 'success'
                              });
                  this.reload()
              }else if(res.data.code==-2){
                  console.log(res.data.msg);
                  return
              }else{
                this.$message.error(`${res.data.msg}`);
                return
              }
            })
          }else{
            this.$message.error('请确认是否删除');
            return
          }
        },
        addcommdity(){
           let data = this.addcommdity1
           if(this.addcommdity1.name==''){
              this.$message.error("商品名称必填");
              return
           }
           if(this.addcommdity1.unit_price==''){
              this.$message.error("商品价格");
              return
           }
          if(this.addcommdity1.position==''){
              this.$message.error("商品产地");
              return
           }
           this.$http.post('/commodity',data).then(res=>{
                if (res.data.code==200){
                    this.$message({
                          message: '商品添加成功',
                          type: 'success'
                          });
                    this.reload()
                }else{
                    this.dialogVisible4 = true
                    this.$message.error(`${res.data.msg}`);
                    return
                }
           })
        },
        handleSizeChange(val) {
          this.pagesize = val;
          console.log(`每页 ${val} 条`);
        },
        handleCurrentChange(val) {
          this.currentPage= val
        },
        modification(item){
          this.dialogVisible2 = true
          item.id=item._id
          this.commodityobj={...item}
        },
        updateuser(){
            let data=this.commodityobj
          
            this.$http.put('/commodity',data).then(res=>{
              if (res.data.code==200){
                    this.$message({
                                message: '信息修改成功',
                                type: 'success'
                                });
                    this.reload()
                }else{
                    this.dialogVisible2 = true
                    this.$message.error(`${res.data.msg}`);
                    return
                }
            })
          },
    },
    watch:{
      search(val){
        if(val==''){
          this.tableData=this.arr
        }else{
          this.tableData=this.tableData.filter(data => !this.search || data.name.toLowerCase().includes(this.search.toLowerCase()))
        }
        return
      }
    }
  }
</script>
<style>
#hig{
  width: 97%;
  height: 583px ;
  margin-left: 20px;
  

}


 .time {
    font-size: 13px;
    color: #999;
  }
  
  .bottom {
    margin-top: 13px;
    line-height: 12px;
  }

  .button {
    padding: 0;
    float: right;
  }

  .image {
    width: 100%;
    height: 100%;
    display: block;
  }

  .clearfix:before,
  .clearfix:after {
      display: table;
      content: "";
  }
  
  .clearfix:after {
      clear: both
      
  }
</style>