<template>
  <div>
    <div id="main" ref="main"></div>
  </div>
</template>

<script>
import resize from 'element-resize-detector'
var b = resize()
export default {
    data(){
        return {
           option: {
                legend: {},
                tooltip: {
                trigger: 'axis',
                showContent: true
                },
                dataset: {
                source: [
                    ['product', '一月', '二月', '三月', '四月', '五月', '六月'],
                    ['奶茶',  32.7,  50.1,56.5,46.1, 53.4, 37.9,],
                    ['拿铁', 14.5, 20.1, 10.3, 10.4, 8.7,18.4],
                    ['芝芝可可', 4.8, 17.2, 10.4, 7, 10.5,4.2],
                    ['圣代',  48.3, 13.6, 21.8, 36.5,28.4,41.3]
                ]
                },
                xAxis: {
                    type: 'category', 
                    axisTick: { show: false },
                    axisLine:{
                            show:true,  
                            lineStyle:{
                            color:'#6FC6F3',  
                            }   
                        }
                
                },
                yAxis: {
                  
                    gridIndex: 0,
                    axisLine:{
                            show:true,  
                            lineStyle:{
                            color:'#6FC6F3',  
                            }   
                        }
                    },
                grid: { top: '30%' },
                series: [
                {
                    type: 'line',
                    smooth: true,
                    seriesLayoutBy: 'row',
                    emphasis: { focus: 'series' }
                },
                {
                    type: 'line',
                    smooth: true,
                    seriesLayoutBy: 'row',
                    emphasis: { focus: 'series' }
                },
                {
                    type: 'line',
                    smooth: true,
                    seriesLayoutBy: 'row',
                    emphasis: { focus: 'series' }
                },
                {
                    type: 'line',
                    smooth: true,
                    seriesLayoutBy: 'row',
                    emphasis: { focus: 'series' }
                },
                ]
           } 

        }
    },
    mounted() {
     this.add()
    },
    methods:{
        add(){
            let a =this.$refs.main
            const myChart = this.$echarts.init(a)
            b.listenTo(a,function(){
                myChart.resize()
            })

            myChart.setOption(this.option);
        }
    }
}
</script>

<style scoped>
#main{
    width: 100%;
    height:500px;
    margin-top: 40px;
}
</style>