<template>
  <div >
    <el-menu :default-active='$route.path' :router="true"  class="el-menu-vertical-demo" :collapse="isCollapse">
      <h3 class="el-h3">{{isCollapse ?'管理系统' : '后台管理系统'}}</h3>
      <div v-for="item in permission_routes">
        <template v-if="!item.hidden">
          <el-menu-item :index=resolve(item)>
              <i :class=item.icon></i>
              <span slot="title">{{item.children[0].meta.title}}</span>
          </el-menu-item>
        </template>
      </div>
    </el-menu>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
  export default {
    data() {
      return {
        route:[]
      };
    },
    created(){
        this.permission_routes.forEach(item=>{
          if(!item.hidden){
           this.route.push(item)
          }
        })
       console.log(this.permission_routes);
    },
    methods: {
     resolve(item){
      let path
      if(item.path=='/'){
        path=item.path+item.children[0].path
      }else{
        path=item.path+'/'+item.children[0].path
      }
      
      return path
     }
    },
     computed:{
      ...mapGetters([
      'permission_routes',
      ]),
      isCollapse(){
        return this.$store.state.tab.isCollapse
      },
      
      
    }
  }
</script>

<style>
.el-menu-vertical-demo:not(.el-menu--collapse) {
    width: 200px;
    min-height: 400px;
  }
.el-h3{
  text-align: center;
  user-select: none;
  font-size: 15px;
}

</style>
