<template>
  <div>
    <div id="main" ref="main"></div>
  </div>
</template>

<script>
import resize from 'element-resize-detector'
var b = resize()
export default {
   data(){
        return {
           option: {
             legend: {},
                tooltip: {},
                dataset: {
                    source: [
                    ['product', '奶茶', '拿铁', '芝芝可可','圣代'],
                    ['一月', 430.3, 250.8, 303.7,403.8],
                    ['二月', 500.1, 273.4, 255.1,362.1],
                    ['三月', 470.4, 265.2, 182.5,368.6],
                    ['四月', 572.4, 253.9, 339.1,382.1]
                    ]
                },
                xAxis: {
                    type: 'category',
                    axisLine:{
                                show:true,  
                                lineStyle:{
                                color:'#6FC6F3',  
                                }   
                            }
                
                },
                yAxis: {axisLine:{
                            show:true,  
                            lineStyle:{
                            color:'#6FC6F3',  
                            }   
                        }},
                // Declare several bar series, each will be mapped
                // to a column of dataset.source by default.
                series: [{ type: 'bar' }, { type: 'bar' }, { type: 'bar' },{ type: 'bar' }]
            }
        } 

    },
    mounted() {
     this.add()
    },
    methods:{
        add(){
             let a =this.$refs.main
            const myChart = this.$echarts.init(a)
            b.listenTo(a,function(){
                myChart.resize()
            })
            myChart.setOption(this.option);
        }
    }
}
</script>

<style scoped>
#main{
    width: 100%;
    height:500px;
    margin-top: 40px;
}
</style>