<template>
<div>
    <el-row :gutter="10">
      <el-col :span="8">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span>个人信息</span>
          </div>
          <el-tooltip class="item" effect="dark" content="上传头像" placement="bottom">
            <el-button type="text" class="el-btn" @click="centerDialogVisible = true">
              <span>
                <img class="head-img" src="../../assets/u=1694074520,2517635995&fm=253&app=138&size=w931&n=0&f=JPEG&fmt=auto.webp">
              </span>
            </el-button>
          </el-tooltip>
          <el-dialog
              title="上传头像"
              :visible.sync="centerDialogVisible"
              width="30%"
              center>
              <span>
                <el-upload
                    :imageUrl="imageUrl"
                    class="avatar-uploader"
                    action="https://jsonplaceholder.typicode.com/posts/"
                    :show-file-list="false"
                    :on-success="handleAvatarSuccess"
                    :before-upload="beforeAvatarUpload">
                    <img v-if="imageUrl" :src="imageUrl" class="avatar">
                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
              </span>
              <span slot="footer" class="dialog-footer">
                <el-button @click="centerDialogVisibleesc">取 消</el-button>
                <el-button type="primary" @click="centerDialogVisibleadd">确 定</el-button>
              </span>
          </el-dialog>
          <div class="grxx">
              <div class="el-icon-s-custom">登录账号</div>
              <div>{{obj.username}}</div>
          </div>
          <el-divider class="br"></el-divider>
           <div class="grxx1">
              <div class="el-icon-unlock">用户权限</div>
              <div>{{obj.quanxian}}</div>
          </div>
          <el-divider class="br"></el-divider>
          <div class="grxx1">
              <div class="el-icon-phone ">电话号码</div>
              <div>{{obj.phone_number}}</div>
          </div>
          <el-divider class="br"></el-divider>
          <div class="grxx1">
              <div class="el-icon-s-cooperation ">账户余额</div>
              <div>{{obj.wallet}}</div>
          </div>
          <el-divider class="br"></el-divider>
        
          <div class="grxx1">
              <div class="el-icon-user-solid ">性别</div>
              <div v-if="obj.gender=='女'">{{obj.gender=='男' ? '男':"女"}}</div>
          </div>
          <el-divider class="br"></el-divider>
           <div class="grxx1">
              <div class="el-icon-date">注册时间</div>
              <div>{{obj.creat_time}}</div>
          </div>
          <el-divider class="br"></el-divider>
          <div style="height:20px">
          </div>
        </el-card>
      </el-col>
      <el-col :span="15">
        <div class="grid-content bg-purple">
        <Rightuser />
        </div>
      </el-col>
    </el-row>
</div>
</template>


<script>
import Rightuser from './Rightuser';
export default {
    inject:['reload','newtime'],
    data() {
        return {
            obj:{},
            imageUrl: '',
            centerDialogVisible: false
        };
    },
    created(){
       this.$store.dispatch('user/getInfo').then(res=>{
        console.log(res);
        res.creat_time=this.newtime(res.creat_time)
        this.obj=res
       })
    },
    methods: {
      centerDialogVisibleesc(){
          this.centerDialogVisible=false
          this.imageUrl=''
          
      },
      centerDialogVisibleadd(){
          this.centerDialogVisible=false
          this.imageUrl=''
      },
      handleAvatarSuccess(res, file) {
        this.imageUrl = URL.createObjectURL(file.raw);
      },
      beforeAvatarUpload(file) {
        const isJPG = file.type === 'image/jpeg';
        const isLt2M = file.size / 1024 / 1024 < 2;

        if (!isJPG) {
          this.$message.error('上传头像图片只能是 JPG 格式!');
        }
        if (!isLt2M) {
          this.$message.error('上传头像图片大小不能超过 2MB!');
        }
        return isJPG && isLt2M;
      }

    },
    components: { Rightuser }
}

</script>

<style>
.grxx1{
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 13px;
}
.grxx{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 80px;
  font-size: 13px;
}
.el-btn{
  width: 30%;
  margin-left: 35% !important;
}
  .el-row {
    margin-bottom: 20px;
 
  }
  
  .el-col {
    border-radius: 4px;
    margin-left: 20px;
    margin-top: 20px;
  }
  .bg-purple-dark {
    background: #99a9bf;
  }
  .bg-purple {
    background: #fff;
    border: 1px solid #eee;
    box-shadow: 1px 1px 10px 1px #eee;
  }
  .bg-purple-light {
    background: #e5e9f2;
  }
  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
  .row-bg {
    padding: 10px 0;
    background-color: #f9fafc;
  }
  .head-img{
    width: 100%;
    height: 100%;
    border-radius: 50%;
  }

.br{
  margin: 13px 0 !important;
}
 .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 100px;
    margin-top: 40%;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>