                    <div class="flex">
                        <el-avatar shape="square" :size="80" src="https://gimg2.baidu.com/image_search/src=http%3A%2F%2Fwww.3158.cn%2Fdata%2Fi10%2F20160701%2F6186605211966040_490x490.jpg&refer=http%3A%2F%2Fwww.3158.cn&app=2002&size=f9999,10000&q=a80&n=0&g=0n&fmt=auto?sec=1660728585&t=576cd3b6089ab53b592e9015e162cb30"></el-avatar>
