            <el-col :span="21">
                <div class="grid-content bg-purple">
                   <Echarts />
                </div>
            </el-col>
           <div style="width:103.2%">
           
            <el-col :span="10">
                <div class="grid-content bg-purple" style="padding-bottom:20px">
                  <Pie />
                </div>
            </el-col>
            <el-col :span="10">
                <div class="grid-content bg-purple" style="padding-bottom:20px">
                <Bar />
