    </div>
</template>
<script>
import Echarts from './Echarts'
import Pie from './Pie.vue'
import Bar from './bar.vue'

class DynamicDigital {
    constructor() {
        this.descDigital = 0
        //起始值
        this.prevDigital = 0
        //时长
        this.step = 0
        this.duration = 20
        this.dom = null
    }
    /*
        descDigital  设定的数值
        duration 时长
        dom 节点
    */
    render(descDigital, duration, dom) {
        this.descDigital = descDigital
        this.duration = duration
        this.dom = dom
    }

    show() {
        //等于设定值时结束
        if (this.prevDigital === this.descDigital) {
            this.step = 0;
            return;
        }

        //设置步长
        if (this.step === 0) {
            const diff = this.descDigital - this.prevDigital;
            const count = this.duration / 50
            this.step = diff > 0 ? Math.ceil(diff / count) : Math.floor(diff / count)
        }
        this.prevDigital += this.step

        //防止数值超出
        if (this.step > 0) {
            this.prevDigital = this.prevDigital > this.descDigital ? this.descDigital : this.prevDigital
        } else {
            this.prevDigital = this.prevDigital < this.descDigital ? this.descDigital : this.prevDigital
        }
        this.dom.innerHTML = this.prevDigital.toLocaleString()
        //每50毫秒绘制一次
        setTimeout(() => {
            this.show()
        }, 50)

    }
}
export default {
    data() {
        let value1 = 32680;
        let value2 = 29550;
        let value3 = 23190;
        let value4 = 14580;
        return {
            arr: [
                {
                    value: value1,
                    moth: "moth1",
                },
                {
                    value: value2,
                    moth: "moth2",
                },
                {
                    value: value3,
                    moth: "moth3",
                },
                {
                    value: value4,
                    moth: "moth4",
                }
            ]
        };
    },
    mounted() {
        this.arr.forEach(item => {
            this.setValue(item.value, item.moth);
        });
    },
    methods: {
        setValue(value1, el) {
            let value = value1;
            let dynamicDigital = new DynamicDigital();
            const dom = document.getElementById(el);
            dynamicDigital.render(value, 2000, dom);
            dynamicDigital.show();
            let t = setInterval(() => {
                value += 500;
                dynamicDigital.render(value, 2000);
                dynamicDigital.show();
            }, 5000);
            setTimeout(() => {
                clearInterval(t);
                t = null;
            }, 4000);
        }
    },
    components: { Echarts, Pie, Bar }
}



</script>
