import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)
import Commodity from '@/views/commodity/Index'
import Dashboard from '@/views/dashboard/Index'
import Sgin from '@/views/sgin/Index'
import Layout from '@/Layout/Index'
import Register from '@/views/register/Register'
import Admin from '@/views/admin/Index'
import User from '@/views/user/Index'
import Userdata from '@/views/user/Userdata'


export const constantRoutes = [
  {
    path: '/sgin',
    component: Sgin,
    hidden: true,
    meta: { title: '登录'}
  },
  {
    path: '/register',
    component: Register,
    hidden: true,
    meta: { title: '注册'}
  },
  // {
  //   path: '/404',
  //   component: () => import('@/views/error-page/404'),
  //   hidden: true
  // },
  // {
  //   path: '/401',
  //   component: () => import('@/views/error-page/401'),
  //   hidden: true
  // },
  {
    path: '/',
    component: Layout,
    redirect: '/dashboard',
    icon:'el-icon-menu',
    children: [
      {
        path: 'dashboard',
        component: Dashboard,
        name: 'Dashboard',
        meta: { title: '首页'}
      }
    ]
  },
  
  
]


export const asyncRoutes = [
  {
    path: '/admin',
    component: Layout,
    redirect: '/admin/index',
    icon:'el-icon-user-solid',
    children: [
      {
        path: 'index',
        component: Admin,
        name: 'admin',
        meta: { title: '用户中心' ,roles: ['admin']}
      }
    ]
  },
  {
    path: '/commdity',
    component: Layout,
    redirect: '/commdity/index',
    icon:'el-icon-document',
    children: [
      {
        path: 'index',
        component:Commodity,
        name: 'commdity',
        meta: { title: '商品详情',roles: ['admin']}
      }
    ]
  },
  {
    path: '/user',
    component :Layout,
    redirect: '/user/index',
    icon:'el-icon-setting',
    children:[
      {
        path:'index',
        component:User,
        meta: { title: '个人中心',roles: ['admin']},
      },
    ]
  }

  // 404 page must be placed at the end !!!
  
]

